.newsLetter{
    padding: 2rem 4rem;
    border: 1px solid var(--color-golden);
    background-color: var(--color-black);
}

.newLetter-heading{
    text-align: center;
    
}

.newsLetter-input{
    flex-direction: row;
    margin-top: 3rem;
}

.newsLetter-input input{
    width: 600px;
    border: 1px solid var(--color-golden);
    border-radius: 5px;
    font-size: 1rem;
    font-family: var(--font-base);
    color: var(--color-white);

    margin-right: 2rem;
    padding: 0.75rem 1rem;
    background-color: var(--color-black);
}

.newsLetter-input button{
    width: max-content;

}

@media screen and (min-width: 2000px) {
    .newsLetter-input input{
        font-size: 2rem;
    }
}


@media screen and (max-width: 990px) {
    .newsLetter-input input{
        flex-direction: column;
        width: 100%;
    }

    .newsLetter-input{
        margin: 0 0 2rem 0;
        width: 100%;
    }
}


@media screen and (max-width: 650px) {
    .newsLetter{
        padding: 2rem 0;
        border: 0;
        width: 100%;
    }
}


@media screen and (max-width: 330px) {
    .newLetter-heading h1{
        font-size: 32px;
        line-height: 50px;
    }
}